body {
  color: #777;
  font-family: 'Comfortaa';
}


label, .pure-button, input, textarea, p {
  font-family: 'Comfortaa';
}

.footer {
  margin-top: 3.4286em;
  margin-bottom: 3.4286em;
  text-align: center;
}

.footer svg {
  margin: 6px;
}

.pure-img-responsive {
  max-width: 100%;
  max-height: 100%;

}

.logo-grid {
  padding-top: 20px
}

.logo-wrapper {
  margin: 12px;
  position: relative
}

.banner {
  max-height: 200px
}

input {
  width: 50%
}

textarea {
  width: 75%;
  height: 200px;
}

.logo-container {
  display: flex; /* Magic begins */
  flex-direction: column;
  justify-content: center; /* Center in main axis */
  align-items: center; /* Center in cross axis */
  flex-flow: row wrap;
  position: relative; /* /* establish neares positioned ancenstor for absolute positioning */
}

.logo-small .after {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: none;
  color: #FFF;
  font-family: 'Comfortaa', cursive;
}

@media (hover: hover) {
    .logo-small:hover .after {
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: center;
        background: rgba(0, 0, 0, .6);
    }
}

.content {
  margin: 0 auto;
  padding: 2em 2em;
  max-width: 1400;
  margin-bottom: 50px;
  line-height: 1.6em;
}

.header-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header {
  margin: 0;
  color: #333;
  text-align: center;
  padding: 0.5em 2em 0;
  border-bottom: 1px solid #eee;
  max-height: 400px;
}

h1 {
  margin: 1em 0;
  font-size: 3em;
  font-weight: 300;
}

h2 {
  font-weight: 300;
  color: #ccc;
  margin-bottom: 0.5em;
}

.content-subhead {
  margin: 50px 0 20px 0;
  font-weight: 300;
  color: #888;
}
